<template>
  <div>
    <div>
      <el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
        <el-form-item label="title">
          <el-input v-model="formLabelAlign.title"></el-input>
        </el-form-item>
        <el-form-item label="picturlUrl">
          <el-input v-model="formLabelAlign.picturl"></el-input>
        </el-form-item>
        <el-form-item label="date">
          <el-date-picker
      v-model="formLabelAlign.date"
      type="datetime"
      placeholder="">
    </el-date-picker>
        </el-form-item>
        <el-form-item label="author">
          <el-input v-model="formLabelAlign.author"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="edit_container">
      <!--   -->
      <quill-editor
        v-model="formLabelAlign.text"
        ref="myQuillEditor"
        :options="editorOption"
        @blur="onEditorBlur($event)"
        @focus="onEditorFocus($event)"
        @change="onEditorChange($event)"
      ></quill-editor>
      <!--  -->
      <div v-html="str" class="ql-editor">{{str}}</div>
      <button @click="send()"></button>
    </div>
  </div>
</template>
<script>
import { addNews } from "../../network/data";
import { quillEditor } from "vue-quill-editor"; //
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";
export default {
  components: {
    quillEditor
  },
  data() {
    return {
      total: 8, //
      text: ``,
      str: "",
      editorOption: {},
      labelPosition: "right",
      formLabelAlign: {
        title: "",
        picturl: "",
        date: "",
        author:'',
        text:'',
      }
    };
  },
  methods: {
    handleCurrentChange(val) {
      console.log(val);
    },
    send() {
      addNews(this.formLabelAlign).then(res=>{
              this.$message({
          showClose: true,
          message: '',
          type: 'success',
          duration:1200
        });
      })
      console.log(this.formLabelAlign);
    },
    onEditorReady(editor) {
      // 
    },
    onEditorBlur() {}, // 
    onEditorFocus() {}, // 
    onEditorChange() {}, // 
    // 
    escapeStringHTML(str) {
      str = str.replace(/&lt;/g, "<");
      str = str.replace(/&gt;/g, ">");
      return str;
    }
  },
  computed: {
    editor() {
      return this.$refs.myQuillEditor.quill;
    }
  },
  mounted() {
    let text = ""; // 
    this.str = this.escapeStringHTML(text);
  }
};
</script>